@font-face {
  font-family: 'RobotoMono';
  src: url('./fonts/RobotoMono-Thin.ttf');
}

* {
  font-family: 'RobotoMono';
}

body {
  align-items: center;
  background: linear-gradient(to right, rgb(83, 105, 118), rgb(41, 46, 73));
  color: #FFF;
  display: flex;
  height: 100vh;
  justify-content: center;
  text-align: center;
}
span {
  font-size: 1.6em;
}
h3 {
  font-size: 1em;
}